<template>
  <div>
              <!-- start page title -->
        <div class="row">
            <div class="col-12">
                <div class="page-title-box d-flex align-items-center justify-content-between">
                    <h4 class="mb-0 font-size-18">Cadastro de Contas Recorrentes</h4>
                    <div class="page-title-right">
                        <router-link :to="{name: 'contasRecorrentesLancar'}" class="btn btn-outline-primary waves-effect waves-light">
                            <i class="bx bxs-plus-square font-size-16 align-middle mr-2"></i> Novo
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
        <!-- end page title -->
        <div class="row" style="padding:10px">
        <div class="col-sm-12 col-md-3">                  
            <label >Mostrar  
            <select class="custom-select custom-select-sm form-control form-control-sm" v-model="filtro.per_page" @input="debounceBusca">
                <option :value="10">10</option>
                <option :value="25">25</option>
                <option :value="50">50</option>
                <option :value="100">100</option>
            </select>
            </label>                  
        </div>
        <div class="col-sm-12 col-md-3">
         &nbsp;       
        </div>
        <div class="col-sm-12 col-md-4">
            <label style="width:90%">Buscar por:
                <input autofocus type="text" class="form-control form-control-sm" placeholder="Descrição" v-model="filtro.texto" @input="debounceBusca" >
            </label>                  
        </div>
        <div class="col-sm-12 col-md-2">
            <label style="width:90%">Filtrar:
            <button @click="navegar(1)" class="btn btn-outline-primary btn-sm btn-block"><span class="bx bx-search-alt"></span></button>
            </label>
        </div>
        </div>
        <div class="row">
            <div class="col-12">
                <div class="card">
                    <div class="card-body">
                        
                        <div class="table-responsive">
                                <table class="table mb-0" >
                                <thead>
                                <tr>
                                    <th>Id</th>
                                    <th>Fornecedor</th>
                                    <th>Descrição</th>
                                    <th>Periodicidade</th>
                                    <th>Futuras</th>
                                    <th>Ativo</th>
                                    <th>Ações</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="registro in dados" :key="registro.id">
                                    <td>{{registro.id}}</td>
                                    <td>{{registro.parceiro.apelido}}</td>
                                    <td>{{registro.descricao }}</td>
                                    <td>{{registro.periodicidade }}</td>
                                    <td>{{registro.qtde_futuras }}</td>
                                    <td>
                                        <span v-if="registro.ativo" class="badge badge-soft-primary">Sim</span>
                                        <span v-else class="badge badge-soft-dark">Não</span>
                                    </td>
                                    <td>
                                        <router-link :to="{name: 'contasRecorrentesEditar', params: {id: registro.id }}" class="btn btn-outline-success waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Editar">
                                            <i class="mdi mdi-pencil d-block font-size-12"></i>
                                        </router-link>
                                    </td>
                                </tr>
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colspan="10000"> 
                                            <paginacao-componente :source="paginacao" @navigate="navegar($event)"></paginacao-componente>
                                        </td>
                                    </tr>
                                </tfoot>
                            </table>
                        </div>

                    </div>
                </div>
            </div> <!-- end col -->
        </div> <!-- end row -->

  </div>
</template>

<script>
import { ContasRecorrentesService } from '../../../services'

const contasRecorrentesService = new ContasRecorrentesService();
export default {

    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'id',
                ordem: 'asc',
                page: 1,
                per_page: 50,
                texto: '',
                tipo: ''
            },
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 50
            },
            debounce: null,
            objetoModal: null,
            sucessoDeletar: null,
            erroDeletar: null
        };
    },
    mounted() {
            this.buscar();
    },
    methods: {
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            contasRecorrentesService.listar(this.filtro).then(
                response => {
                    this.$data.dados = response.data.data;
                    this.$data.paginacao = response.data;
                    this.$data.filtro.page = response.data.current_page;
                    this.$data.paginacao.per_page = response.data.per_page;
                }
            )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
  
    }
};
</script>
<style scoped>
    tr:hover{
        background-color: #eff2f7 ;
    }
</style>
